@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,400&display=swap);
.Dog img{width:75%;border-radius:50%}

.Dog img {
  width: 75%;
  -webkit-filter: grayscale();
          filter: grayscale();
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  -webkit-transition: 0.4s -webkit-filter ease;
  transition: 0.4s -webkit-filter ease;
  transition: 0.4s filter ease;
  transition: 0.4s filter ease, 0.4s -webkit-filter ease;
}

.Dog:hover img {
  -webkit-filter: none;
          filter: none;
}

.Dog .underline {
  text-decoration: none;
  border: 4px solid transparent;
  display: inline-block;
  vertical-align: top;
  line-height: 36px;
  text-transform: uppercase;
  color: black;
  letter-spacing: 0.2em;
  text-align: center;
  font-size: 1.4em;
  margin: 10px;
  position: relative;
}

.underline::after {
  width: 0%;
  height: 4px;
  display: block;
  background-color: #fff;
  content: "";
  position: absolute;
  top: 34px;
  left: 50%;
  -webkit-transition: left 0.4s cubic-bezier(0.215, 0.61, 0.355, 1),
    width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: left 0.4s cubic-bezier(0.215, 0.61, 0.355, 1),
    width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.Dog:hover .underline:after {
  width: 100%;
  left: 0;
}

.DogDetails-card {
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

body{text-align:center;background:#e96443;background:-webkit-gradient(linear, left top, right top, from(#904e95), to(#e96443));background:linear-gradient(to right, #904e95, #e96443);min-height:100vh;font-family:"Source Sans Pro", sans-serif}

